@import url('https://fonts.googleapis.com/css?family=Numans');

.loginPage {
    text-align: center;
    align-content: center;
    background-image: url('http://getwallpapers.com/wallpaper/full/a/5/d/544750.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    font-family: 'Numans', sans-serif;
}

.card {
    height: 370px;
    margin-top: auto;
    margin-bottom: auto;
    width: 400px;
    background-color: rgba(0, 0, 0, 0.5) !important;
}

.errorMessage {
    position: absolute;
    color: red;
    right: 5%;
    top: 24%
}

.formBlock {
    width: 80%;
    position: relative;
}

.social_icon span {
    font-size: 60px;
    margin-left: 10px;
    color: #FFC312;
}

.social_icon span:hover {
    color: white;
    cursor: pointer;
}

.cardHeader h3 {
    padding-top: 2rem;
    color: white;
}

.social_icon {
    position: absolute;
    right: 20px;
    top: -45px;
}

.inputGroupPrepend span {
    margin: auto 10px;
    height: 100%;
    background-color: #FFC312;
    color: black;
    border: 0 !important;
}

input:focus {
    outline: 0 0 0 0 !important;
    box-shadow: 0 0 0 0 !important;

}

.remember {
    color: white;
}

.remember input {
    width: 20px;
    height: 20px;
    margin-left: 4rem;
    margin-right: 5px;
}

.login_btn {
    color: black;
    background-color: #FFC312;
    width: 100px;
}

.login_btn:hover {
    color: black;
    background-color: white;
}

.footerSentence {
    color: white;
}

.footerSentence a {
    margin-left: 4px;
}