.profile {
    /* border-left: solid rgb(47, 51, 54) 1px;
    border-right: solid rgb(47, 51, 54) 1px; */
}

.profile_background {
    background-image: url('https://pbs.twimg.com/profile_banners/4463597177/1449960305/600x200');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 200px;
    width: 100%;
}


.profile_photo {
    background-image: url('https://images.unsplash.com/photo-1555445091-5a8b655e8a4a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 10em;
    height: 10em;
    border-radius: 100%;
    border: solid 1px white;
    position: absolute;
    top: 20%;
    left: 62%;
}

.profile_about {
    border-bottom: solid 1px black;
    height: 120px;
    padding: 5px 5px;
}